.photoGallery {
    margin-top: -20px;
    padding-top: 100px;
    // padding-bottom: 50px;
    background-color: #222831;
    min-height: 43vh;

    h2 {
        font-size: 28px;
        text-align: center;
        color: white;
        margin-bottom: 50px;
    }

    .wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}

@media(max-width: 1200px) {
    .photoGallery {
        padding-top: 70px;
    }
}
@media(max-width: 576px) {
    .photoGallery {
        padding-top: 60px;
    }
}