ul li a span.iconic, ul li span.iconic{
    top: 1px;
    position: absolute;
    left: 10px;
}

@media(max-width:768px){
    ul li a span.iconic, ul li span.iconic{
        position: unset;
        padding-right: 10px;
    }
}
@media(max-width:576px){
    /* ul li:first-child span.iconic{
        padding-right: 0;
        position: relative;
        left: 18px;
    } */
    ul li:first-child{
        display: block;
    }
}