.newsItem {
    display: flex;
    flex-flow: column;
    margin-top: 30px;
    box-shadow: 0 2px 15px 5px rgba(0, 0, 0, 0.12), 0 2px 10px 0 rgba(0, 0, 0, 0.01);
    padding: 30px 15px;
    border-radius: 0.25rem;
    margin-bottom: 20px;
    position: relative;

    &:hover {
        box-shadow: 0 2px 15px 5px rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.1);
    }

    &:first-child {
        margin-left: 0;
    }

    &:last-child {
        margin-right: 0;
    }

    img {
        width: 100%;
        height: 170px;
        margin-top: -50px;
        object-fit: cover;
        border-radius: 0.25rem;
        box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.28), 0 4px 15px 0 rgba(0, 0, 0, 0.05);
    }

    a {
        padding-top: 15px;
        line-height: 1.2;
        font-weight: initial;
    }

    & .newsDate {
        position: absolute;
        bottom: 10px;
        left: 15px;
        font-size: 15px;
        line-height: 1.2;
        font-weight: 600;
    }

    & .newsOne {
        position: absolute;
        bottom: 10px;
        right: 15px;
        font-size: 15px;
        font-weight: 600;
    }
}

.cardBottom {
    padding-top: 20px;
}

.content {
    height: 4.5em;
    text-overflow: ellipsis;
    overflow: hidden;
    position: relative;
    &::after {
        content: "";
        text-align: right;
        position: absolute;
        bottom: 0;
        right: 0;
        width: 40%;
        height: 1.2em;
        background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 50%);
    }
}

@media (max-width: 1200px){
    .newsItem {
        img{
            height: 140px;
        }
    }
    .content {
        height: 3.2em;
    }
}
@media (max-width: 992px){
    .newsItem {
        box-shadow: unset;
        border: 1px solid rgba(0, 0, 0, 0.12);
        &:hover{
            box-shadow: unset;
        }
        img{
            height: 200px;
        }
    }
    .content {
        height: 4.5em;
    }
}
@media (max-width: 768px){
    .newsItem {
        img{
            height: 170px;
        }
    }
    
}
@media (max-width: 576px){
    .newsItem {
        img{
            height: 250px;
        }
    }
    
}

@media (max-width:400px) {
    .newsItem {
        img{
            height: 200px;
        }
    }
}
