.contact-container {
    background-color: #222831;
    padding:100px 0 60px;
}

.contact-header {
    font-weight: 600;
    font-size: 28px;
    color: #FFFFFF;
    text-align: center;
    margin-bottom: 3rem;
}


.org-map {
    /*width: 800px;*/
    height: 500px;
}

.contact-input  {
    width: 100%;
    margin-bottom: 1.5rem;
    border: 1px solid #38394e;
    background-color: #232430;
    border-radius: 5px;
    height: 40px;
    padding-left: .5rem;
    color: #FFFFFF;
}

::placeholder {
    color: #bdbdbd;
}

.contact-form {
    border: 1px solid #424242;
    background-color: #252A35;
    padding: 2rem 2rem 4rem 2rem;
    border-radius: 5px;
    box-shadow: 0 -2px 20px -9px rgba(0,0,0,0.75);
}

.message {
    width: 100%;
    margin-bottom: 1rem;
    border: 1px solid #38394e;
    background-color: #232430;
    border-radius: 5px;
    padding: .5rem;
    color: #FFFFFF;
}

.send-button {
    width: 150px;
    height: 40px;
    float: right;
    border-radius: 5px;
    border: 1px solid #333336;
    background-color: #232430;
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media(max-width:1200px){
    .contact-form{
        padding: 1rem 1rem 4rem 1rem;
    }
}
@media(max-width:992px){
    .contact-container{
        padding-bottom: 50px;
    }
    .contact-form{
        margin-top: 30px;
    }
}
@media(max-width:576px){
    .map-container{
        height: 400px;
    }
}


