@mixin width {
    width: 75%;
}

.news {
    padding-top: 120px;
    padding-bottom: 30px;
    background-color: #222831;

    h2 {
        color: white;
        font-size: 32px;
        margin-bottom: 30px;
    }

    color: white;

    .newsContent {
        padding-bottom: 50px;

        div {
            text-align: center;
        }

        img {
            padding: 20px 0;
            object-fit: cover;
            max-width: 100%;
        }

        p {
            color: white;
            line-height: 1.3;
            font-size: 18px;
            img{
                display: block;
            }
        }

        a {
            color: dodgerblue;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .recentNews {
        padding: 80px 15px 15px 15px;
        // padding-top: 80px;
        position: sticky;
        top: -50px;

        h5 {
            color: white;
            font-size: 20px;
            margin-top: 0;
            text-align: center;
            margin-bottom: 30px;
        }

        .newsCard {
            margin-bottom: 30px;
            display: block;
            color: white;

            &:hover {
                text-decoration: underline;
            }

            img {
                width: 100%;
                height: 156px;
                object-fit: cover;
            }

            p {
                margin: 10px 0;
                color: white;
                font-size: 16px;
                overflow: hidden;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 3;
            }
        }
    }

}

.manyRead {
    padding-top: 30px;
}

@media(max-width:1200px) {
    .news {
        padding-bottom: 0;

        .recentNews {
            padding: 80px 0 15px 0;

            .newsCard {
                p {
                    -webkit-line-clamp: 2;
                }
            }
        }
    }

}

@media(max-width:992px) {
    .news {
        padding-top: 140px;

        .recentNews {
            padding: 0 0 15px 0;

            h5 {
                margin-bottom: 50px;
            }

            .newsCard {
                display: flex;

                img {
                    width: 30%;
                    height: 120px;
                }

                p {
                    padding-left: 20px;
                    width: 60%;
                    -webkit-line-clamp: 3;
                    height: 4.5em;
                }
            }
        }
    }

}

@media(max-width:576px) {
    .news {
        padding-top: 110px;

        h1 {
            font-size: 24px;
        }

        .newsContent {
            p {
                font-size: 16px;
            }
        }

        .recentNews {
            .newsCard {
                img {
                    width: 40%;
                }

                p {
                    margin: 10px 0;
                    width: 100%;
                    padding-left: 15px;
                }
            }
        }
    }

}