.travelItem {
    display: flex;

    div {
        background-color: rgba(0, 0, 0, 0.3);
        padding: 20px;
        width: 100%;
        display: flex;
        align-items: center;

        h5 {
            // color: white;
            color: #fff;
            position: relative;
            font-size: 20px;
            margin-top: 0;
            margin-bottom: 10px;
            font-weight: bold;
            overflow: hidden;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
        }

        // h5::after{
        //     content: "";
        //     position: absolute;
        //     width: 70%;
        //     bottom: -5px;
        //     left: 0;
        //     background-color: #FF9A00;
        //     height: 2px;
        // }
        p {
            color: white;
            font-size: 16px;
        }
    }

    img {
        // width: 220px;
        width: 40%;
        height: 145px;
        object-fit: cover;
    }

    margin-bottom: 20px;
}

@media (max-width:1200px) {
    .travelItem {
        img {
            width: 170px;
        }
    }
}

@media (max-width:576px) {

    .travelItem {
        div {
            h5 {
                font-size: 18px;

                // width: max-content;
                &::after {
                    width: 100%;
                }
            }
        }
    }
}

@media (max-width:450px) {

    .travelItem {
        img {
            width: 140px;
            height: 110px;
        }

        div {
            padding: 10px;

            h5 {
                font-size: 18px;
                width: auto;

                &::after {
                    display: none;
                }
            }

            p {
                font-size: 14px;
            }
        }
    }
}

@media (max-width:330px) {

    .travelItem {
        img {
            width: 120px;
        }

        div {
            padding: 10px;

            h5 {
                font-size: 16px;
            }

            p {
                font-size: 12px;
                margin-bottom: 0;
            }
        }
    }
}