.PContent{
	background-color: #222831;
	color:white;
	padding:50px 0;
}
.PContent pre{
	color:white !important;
}
.head{
	display: inline-block;

}
.head h1{
	margin:0;
	margin-bottom:10px;
}
.head h1:after{
	content:" ";
	display: block;
	height: 4px;
	border-radius: 3px;
	background-color: #FF7315;
	margin-top:5px;
}
.time span:nth-child(1){
	border-right:1px solid #FF7315;
	padding-right:5px;
}

.time span:nth-child(2){
	padding-left:5px;
}
.description p{
	font-size: 18px;
}

@media(max-width:768px){
	.PContent{
		padding: 30px 0;
	}
}
@media(max-width:576px){
	.PContent{
		padding: 20px 0;
	}
	.description p{
		font-size: 16px;
	}
}