.programs {
    .slider-wrapper {
        height: 70vh;

        .previousButton {
            bottom: 0;
            top: unset;
            left: 45%;
        }

        .nextButton {
            bottom: 0;
            top: unset;
            right: 45%;
        }

        .slide {
            background-position: 0 50px !important;
            background-size: 500px 415px !important;

            .inner {
                text-align: center;
                top: 60px;

                .date {
                    background: unset;
                    font-size: 26px;
                    filter: unset;
                    -webkit-filter: unset;
                    margin-left: 40px;
                    span {
                        &::after {
                            height: 18px;
                        }
                    }
                }

                h3 {
                    max-width: unset;
                    margin-bottom: 20px;
                }
            }
        }
    }
}

@media (max-width:576px){
    .programs {
        .slider-wrapper {
            
            .slide {
                .inner {

                    .date {
                        font-size: 16px;
                        span {
                            &::after {
                                height: 14px;
                            }
                        }
                    }
                }
            }
        }
    }
}