.programsInside {


    .slider-wrapper {
        padding: 120px 0;
        background-color: #1b262c;

        .previousButton {
            bottom: 0;
            top: unset;
            left: 46%;
        }

        .nextButton {
            bottom: 0;
            top: unset;
            right: 46%;
        }

        .slide {
            background-position: 0 100px !important;
            background-size: 500px 415px !important;

            .inner {
                text-align: center;
                top: 60px;

                .date {
                    background: unset;
                    font-size: 26px;
                    filter: unset;
                    -webkit-filter: unset;
                    margin-left: 40px;

                    span {
                        &::after {
                            height: 18px;
                        }
                    }
                }

                h3 {
                    max-width: unset;
                    margin-bottom: 20px;
                }
            }
        }
    }
}