.root{
	background-color: #1b262c;
	display: flex;
	align-items: center;
    justify-content: center;
    padding-top: 120px;
    padding-bottom: 50px;
}

.root img{
    width: 550px;
    height: 450px;
    object-fit: contain;
}
.name{
	color: #FF7315;
    font-size: 40px;
    margin-top: 0;
    margin-bottom: 25px;

}
.linkWrap a{
	color: white !important;
    background-color: red;
    display: inline-block;
    border-radius: 5px;
    padding: 5px 27px;
    font-size: 20px;
    font-weight: normal;
    margin-top: 28px;
}
.linkWrap a:hover{
	color: white;
    background-color: red;
}

.pinfo{
    padding-top: 100px;
    padding-left: 50px;
}

@media(max-width:992px){
    .root img{
        width: 100%;
    }
    .pinfo{
        padding-left: 0;
    }
}
@media(max-width:768px){
    .root{
        padding-bottom: 0;
    }
    .root img{
        height: 380px;
    }
    .myRow{
        flex-direction: column-reverse;
    }
    .pinfo{
        padding-top: 0;
    }
}
@media(max-width:576px){
   .name{
       font-size: 32px;
   }
   .linkWrap a{
       font-size: 16px;
   }
   .myRow{
       width: 100%;
   }
   .pinfo{
       padding-left: 15px;
   }
}