.root{
	color: white;
	text-align:center;
	font-size: 40px;
	padding-bottom:55px;
	padding-top:140px;
	background-blend-mode: color-dodge;
	background-attachment: fixed;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
}

.overlay{
	position: absolute;
	height: 100%;
	width: 100%;
	background-color:#222831ad;
	top: 0;
}


.root h1{
	margin: 0;
    font-size: 40px;
    border-bottom:2px solid #FF7315;
    display:inline-block;
	font-weight: bold;
	position: relative;
	z-index: 2;
}

@media(max-width: 576px){
	.root h1{
		font-size: 32px;
	}
}