.news {
    padding: 30px 0 50px 0;

    &Top {
        display: flex;
        justify-content: space-between;
        align-items: center;

    }

    & .newsNavigator {
        font-size: 20px;
    }

    .mainNews {
        padding-top: 55px;
        display: flex;
        justify-content: space-between;
    }
}

// .myContainer{
//     max-width: 1225px !important;
// }

@media (max-width:992px) {
    .news {
        padding: 30px 0;

        .mainNews {
            padding-top: 45px;
        }
    }

}
@media (max-width:576px) {
    .news {
        padding: 10px 0;
        .newsNavigator {
            font-size: 16px;
        }
        .mainNews {
            padding-top: 20px;
        }
    }

}