.root {
    color: white;
    margin-bottom: 35px;
    cursor: pointer;
    background-color: rgba(49, 60, 68, .5);
}
.root:last-child {
    margin-bottom: 0;
}

.root a {
    color: white;
}

.root:hover {
    background-color: #313c44;
    transition: .5s;
}

.root img {
    max-width: 100%;
}

.name h2 {
    padding: 0 0 3px;
    font-size: 26px;
    font-weight: 400;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    max-height: 3.5em;
}

.time {
    display: inline-block;
    background-color: #ffffff33;
    padding: 10px 20px;
}

.time span:nth-child(1) {
    border-right: 1px solid white;
    padding-right: 10px;
}

.time span:nth-child(2) {
    padding-left: 10px;
}


.link {
    color: white;
    font-size: 24px;
    letter-spacing: 7px;
}


.imgWrap img {
    width: 100%;
    height: 200px;
    object-fit: cover;
}
.programsImg img {
    padding-top: 10px;
    object-fit: contain;
}

/* .imgWrap img{
	width:100%;
	height: 125px;
	object-fit: cover;
} */

.content {
    display: flex;
    flex-flow: column;
	justify-content: center;
    height: 100%;
    padding-right: 10px;
}

.linkWrape {
    text-decoration: none !important;
    color: white !important;
}

.desc {
    font-weight: 100;
    font-size: 18px;
    display: none;
}

.type {
    font-family: sans-serif;
    font-size: 18px;
    font-weight: 100;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    max-height: 2.8em;
    /*margin-bottom:15px;*/
}
.type p{
    margin-bottom: 0;
}

@media(max-width:992px){
    .name h2{
        font-size: 24px;
    }
    .imgWrap img{
        height: 180px;
    }
}
@media(max-width:768px){
    .name h2{
        font-size: 20px;
        
    }
    .type{
        font-size: 16px;
    }
}
@media(max-width:576px){
    .imgWrap img{
        height: 200px;
    }
    .content{
        padding: 15px;
    }
}