.teleshows {
    background-color: #222831;
}

.wrapper {
    padding: 10px 0;
    background-color: #0F4C75;
}

.buttons {
    display: flex;
    justify-content: space-between;
}

.button {
    text-align: center;
    cursor: pointer;
    display: flex;
    flex-flow: column;
}

.button button {
    padding: 0;
    color: white;
    font-size: 18px;
    font-weight: bold;
}

.button button:hover,
.button button:focus {
    text-decoration: none;
    color: white;
}

.button span {
    color: white;
    opacity: .6;
    font-size: 14px;
    position: relative;
    top: -8px;
}

.items {
    padding: 50px 0;
}

.item {
    background-color: #0F4C75;
    width: 70%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    padding: 0 10px;
    margin-bottom: 20px;
}

.item div:first-child {
    color: #FF7315;
    font-weight: bold;
    border-right: 2px solid;
    padding-right: 10px;
}

.item div:nth-child(2) {
    padding-left: 10px;
}

.item div:nth-child(2) p {
    margin-bottom: 0;
    color: white;
    font-size: 18px;
    font-weight: bold;
    margin-top: 8px;
}

.item div:nth-child(2) span {
    color: white;
    opacity: .6;
    font-size: 14px;
    position: relative;
    top: -8px;
}

@media(max-width:768px) {
    .buttons {
        flex-wrap: wrap;
        justify-content: center;
    }

    .button {
        margin-left: 15px;
        margin-right: 15px;
    }
}

@media(max-width:576px) {
    .items {
        padding: 30px 0;
    }

    .item {
        width: 100%;
    }
}