.travels {
    background-image: url('../../assets/images/travelsBg.jpg');
    background-repeat: no-repeat;
    padding: 70px 0 65px 0;
    background-position: center;
    background-size: cover;

    .title {
        font-size: 65px;
        color: white;
        margin: 0;
        text-align: center;
        padding-bottom: 50px;
    }

    .wrapper {
        display: flex;
        justify-content: space-between;

        .mainTravel {
            width: 50%;
            padding-right: 15px;
            display: flex;
            align-items: center;

            div {
                padding: 20px;
                background-color: rgba(0, 0, 0, 0.3);
                position: relative;
                top: -3px;

                h5 {
                    // color: white;
                    font-size: 24px;
                    margin-top: 0;
                    margin-bottom: 10px;
                    color: #fff;
                    position: relative;
                    font-weight: bold;
                    // width: max-content;
                }

                // h5::after {
                //     content: "";
                //     position: absolute;
                //     width: 100%;
                //     bottom: -5px;
                //     left: 0;
                //     background-color: #FF9A00;
                //     height: 2px;
                // }

                p {
                    color: white;
                    font-size: 18px;
                }
            }
            a{
                width: 100%;
            }
            img {
                width: 100%;
                height: 360px;
                object-fit: cover;
            }
        }

        .otherTravels {
            width: 45%;
            padding-left: 15px;
        }
    }
}

@media (max-width:1200px) {
    .travels {
        padding: 40px 0;

        .title {
            font-size: 50px;
        }

        .wrapper {
            justify-content: unset;

            .mainTravel {
                img {
                    height: 300px;
                }

            }
        }
    }

}

@media (max-width:992px) {
    .travels {
        padding: 30px 0;

        .title {
            font-size: 40px;
            padding-bottom: 40px;
        }

        .wrapper {
            flex-flow: column;
            align-items: center;

            .mainTravel {
                width: 80%;
                padding-right: 0;
            }

            .otherTravels {
                width: 80%;
                padding-left: 0;
                padding-top: 30px;
            }
        }
    }

}

@media (max-width:767px) {
    .travels {
        .title {
            font-size: 36px;
        }

        .wrapper {
            .mainTravel {
                width: 100%;
            }

            .otherTravels {
                width: 100%;
            }
        }
    }

}

@media (max-width:576px) {
    .travels {
        .title {
            font-size: 28px;
        }

        .wrapper {
            .mainTravel {
                a {
                    width: 100%;
                }

                img {
                    height: 220px;
                }
            }

        }
    }

}

@media (max-width:330px) {
    .travels {
        .title {
            font-size: 36px;
        }

        .wrapper {
            .mainTravel {
                div {
                    padding: 10px;
                    h5 {
                        width: auto;
                        &::after{
                            display: none;
                        }
                    }
                }

            }

        }
    }

}