.galleryItem {
    padding: 150px 0 50px 0;
    background-color: #222831;
}

.galleryItem h2 {
    font-size: 28px;
    text-align: center;
    color: white;
    margin: 50px 0;
    margin-top: 0;
}

.galleryItem img {
    width: 100%;
    height: 250px;
    margin-bottom: 30px;
    object-fit: cover;
}

@media(max-width:576px) {
    .galleryItem {
        padding-bottom: 0;
    }

}