.footer{
    padding: 50px 0 15px 0;
    background-color: #1B262C;
    &Main{
        display: flex;  
        justify-content: space-between;
        border-bottom: 1px solid white;
        padding-bottom: 30px;
        .logo{
            display: flex;
            width: 30%;
            h2{
                color: white;
                font-size: 28px;
                padding-left: 25px;
                margin: 14px 0;
            }
        }
        .footerRight{
            ul{
                padding: 0;
                margin: 0;
                li.socialFooter ul li{
                        padding: 0;
                }
                li.socialFooter{
                    padding-left: 10px;
                }
                li{
                    line-height: 1.8;
                    list-style: none;
                    display: flex;
                    align-items: flex-start;
                    // margin-bottom: 20px;
                    // &:first-child{
                    //     margin-left: -8px;
                    // }
                    padding-left: 35px;
                    position: relative;
                    // img{
                    //     object-fit: contain;
                    //     width: 22px;
                    // }
                    span, a, p{
                        color: white;
                        font-size: 18px;
                    }
                    p{
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    &Bottom,     &Bottom p{
        text-align: center;
        color: white;
        margin-bottom: 0;
        margin-top: 10px;
    }
}

@media (max-width:1200px){
    .footer{
        &Main{
            .logo{
                width: 36%;
                h2{
                    font-size: 25px;
                }
            }
            .footerRight{
                width: 30%;
            }
        }
        
    }
   
}
@media (max-width:992px){
    .footer{
        &Main{
            .logo{
                flex-flow: column;
                width: 30%;
                h2{
                    font-size: 22px;
                    margin: 0;
                    padding-left: 0;
                }
            }
            .footerRight{
                width: 34%;
            }
        }
        
    }
   
}
@media (max-width:768px){
    .footer{
        &Main{
            flex-flow: column;
            align-items: center;
            .logo{
                flex-flow: column;
                align-items: center;
                width: 100%;
            }
            .footerMiddle{
                width: 100%;
                text-align: center;
                padding-top: 20px;
            }   
            .footerRight{
                width: 100%;
                padding-top: 20px;
                ul{
                    li{
                        justify-content: center;
                        padding-left: 0;
                        span.iconic{
                            position: unset;
                        }
                    }
                    li.socialFooter{
                        padding-left: 0;
                    }  
                }   
            }
        }
        
    }
   
}
@media (max-width:576px){
    .footer{
        &Main{
            .logo{
                text-align: center;
            } 
            .footerRight{
                ul{
                    li{
                        text-align: center;
                    }
                }   
            }
        }
        
    }
   
}